<template>
    <header>
        <nav class="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
            <a class="navbar-brand">{{ brand }}</a>
            <button class="navbar-toggler" id="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbar-content" aria-controls="navbar-content" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbar-content">
                <ul class="navbar-nav mr-auto">
                    <slot></slot>
                </ul>
            </div>
        </nav>
    </header>
</template>

<script>
export default {
  name: 'MainHeaderMenu',
  props: {
    brand: String,
    emitter: Object
  },
  mounted: function () {
    this.emitter.on('click-list', function () {
      var togglerButton = document.getElementById('navbar-content')
      togglerButton.classList.remove('show')
      togglerButton.classList.add('hide')
    })
  }
}
</script>
